import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import ColorCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/color/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/colors"

class CreateColor extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createColor } = this.props;
    createColor();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.color.error) {
      notification("error", this.props.color.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.color.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { storeColor } = this.props;
    storeColor(value);
  };

  render() {
    // console.log(this.props);

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="colors.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <ColorCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            color={this.props.color}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    color: state.color
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateColor);
